<template>
  <div class="Top">
    <a v-for="item in items" :key="item.id" :href="item.link">
      <div>
        <div>{{ item.title }}</div>
        <div>{{ item.description }}</div>
      </div>
    </a>
  </div>
</template>

<script>
const topJson = import(/* webpackPrefetch: true */ '../../content/summary.json')
//import topJson from '../../content/top.json'

export default {
  name: 'Top',
  data() {
    return { items: [] }
  },
  mounted() {
    const pageRoot = '/githubpagesTest/post'

    topJson.then(data => {
      for (var i = 0; i < Object.keys(data).length - 1; i++) {
        const readData = data[i]

        console.log(pageRoot + readData.linkPath)
        this.items.push({
          id: i,
          link: pageRoot + readData.linkPath,
          title: readData.title,
          description: readData.description
        })
      }
    })
  }
}
</script>

<style scoped></style>
